*{
    margin: 0;
    padding: 0;
    
}
@font-face {
    font-family: 'Gistesy';
    src: url('../materials/Gistesy.ttf');
}
Link{
    text-decoration: none;
}
.spin-parent{
    /* border: 2px solid red; */
    background-color: #171717;
    height: 100vh;
    width: 100vw;
}
.spin{
    /* border: 2px solid red; */
    margin: auto;
    width: fit-content;
    height: fit-content;
    padding-top: 40vh;
}
.home{
    /* border: 2px solid red; */
    background-color: #171717;
    /* background-color: #1d1d1f; */
    color: white;
}
.head{
    /* border:  2px solid red; */
    /* margin-top: 15px; */
    padding-top: 15px;
    align-items: center;
    text-align: center;
    font-size: 1rem;
    width: 100vw;
}
.head h3{
    font-weight: 100;
    font-family: 'Open Sans', sans-serif;
}
.head-links{
    color: white;
    text-decoration: none;
}
.first-cont{
    margin: 0;
    /* border:  2px solid blue; */
    height: 100vh;
    width: 100vw;
}
.center-div{
    /* border: 2px solid black; */
    width: fit-content;
    margin: auto;
    margin-top: 25vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
}
.center-div h1{
    font-size: 6rem;
    font-family: 'Gistesy',sans-serif;
    letter-spacing: 2px;
    font-weight: 100;
    text-align: center;
    animation: name .5s ease-in;
}
@keyframes name {
    0%{
        opacity: 0;
        transform: translateX(-40px);
    }
    /* 30%{
        opacity: 1;
        transform: translateX(-30px);
    } */
    100%{
        transform: translateX(0);
    }
}

.center-div h2{
    /* border: 2px solid blue; */
    font-weight: 300;
    /* text-align: right; */
    margin-left: 15em;
    font-family: 'Open Sans', sans-serif;
    animation: typewriter 9s normal;
    overflow: hidden;
    white-space: nowrap;
}
@keyframes typewriter{
    0%{
        width: 0;
    }
    10%{
        width: 0;
    }
    100%{
        width: 90%;
    }
  }
.center-div hr{
    width: 30vw;
    text-align: center;
    margin-left: 13rem;
    position: relative;
    top: -5px;
    height: .5px;
    background-color: white;
    border: none;
    animation: hr 1s ease-in;
}
@keyframes hr {
    0%{
        opacity: 0;
        transform: translateX(-20px);
    }
    50%{
        opacity: 0;
        transform: translateX(-10px);
    }
    90%{
        opacity: 1;
        transform: translateX(0);
    }
}

.center-div span{
    user-select: none;
    margin-top: 20vh;
    /* border: 2px solid red; */
    width: 100vw;
    text-align: center;
    animation: arrow 1s ease-in;
    animation-iteration-count: 6;
}
@keyframes arrow {
    0%{
        color: white;
        transform: translateY(-20px);
    }
    80%{
        color: white;
        transform: translateY(-5px);
        opacity: 0.3;
    }
    100%{
        color: #171717;
        transform: translateY(0);
        opacity: 0;
    }
}
.second{
    /* border: 2px solid red; */
    /* height: 90vh; */
    width: 100vw;
    /* background-color: #171717; */
    padding: 30vh 0;
}

.para{
    /* border: 2px solid blue; */
    width: fit-content;
    margin: auto;
    /* margin-top: 30vh; */
    display: flex;
    flex-direction: column;
    align-items: center;
    color: white;
    margin-top: 0vh;
}
.para p{
    font-size: 1.4rem;
    font-family: 'Antic', sans-serif;
    text-align: center;
    letter-spacing: 1px;
}
.para hr{
    width: 20vw;
    margin-top: 1rem;
    margin-left: 35vw;
    height: 2px;
    background-color: white;
    /* background-color: #ff914d; */
    border: none;
}
.para h1{
    margin-left: 55vw;
    font-weight: 100;
    font-size: 3rem;
    font-family:'Antic', sans-serif ;
}
.project {
    /* border: 2px solid blue;    */
    height: fit-content;
    width: 100vw;
    /* background-color: #1d1d1f; */
    background-color: #171717;
    color: white;
}
.project .title{
    /* border: 2px solid red; */
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 2rem;
}
.project .title h1{
    font-size: 3.5rem;
    font-family: 'Open Sans', sans-serif;
    font-weight: 100;
}
.project .title hr{
    width: 10vw;
    margin-left: 10rem;
    border: none;
    height: 3px;
    /* background-color: #ff914d; */
    background-color: white;
    animation: hr 1s ease-in;
}
.project-base-1{
    /* border:  2px solid blue; */
    padding: 3rem 10vw;
    display: flex;
    justify-content:space-around;
    
}
.project-comp{
    /* border:  2px solid red; */
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px 3rem;
    text-align: center;
    border-radius: 70px;
    transition: .3s;
}
.project-comp h1{
    font-family: 'Alata', sans-serif;
    font-weight: 100;
    transition: .2s;
}
.project-comp:hover{
    background-color: rgb(31, 31, 31);
}
.project-comp p{
    font-weight: 100;
    font-family: 'Antic', sans-serif;
    margin-top: 1rem;
    letter-spacing: 1px;
}
.project-comp img{
    width: 11vw;
    height: 22vh;
    margin: 1rem 0;
    user-select: none;
}
#wide{
    width: 25vw;
    
}
#not_wide{
    width: 20vw;
}
.tag{
    /* border: 2px solid red;    */
    margin-top: 1rem;
    width: 20vw;
}
.tag li{
    border: 1px solid white;
    background-color: rgb(36, 36, 36);
    border-radius:10px ;
    padding: 4px 9px;
    margin: .5rem;
    display: inline-block;
    font-weight: 100;
    width: fit-content;
    font-size: 13px;
}
.hireme{
    /* border: 2px solid red; */
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: 2.6rem;
    padding: 3rem 0;
    padding-bottom: 8rem;
}
.hireme h1{
    font-family: 'Open Sans', sans-serif;
    font-weight: 100;
    
}
.hireme hr{
    margin-left: 15rem;
    width: 15vw;
    height: 3px;
    border: none;
    /* background-color: #ff914d; */
    background-color: white;
    
}

.contact{
    border: 2px solid red;
    height: 100vh;
    width: 100vw;
    background-color: #171717;
    color: white;
    font-family: 'Open Sans', sans-serif;
}
.contact .title{
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 2rem;
    
}
.contact .title h1{
    font-size: 3.2rem;
    font-family: 'Open Sans', sans-serif;
    font-weight: 100;
    
}
.contact .title hr{
    width: 15vw;
    margin-left: 10rem;
    border: none;
    height: 2px;
    /* background-color: #ff914d; */
    background-color: white;
    animation: hr 1s ease-in;
}
.contact form{
    /* border: 2px solid blue; */
    display: flex;
    flex-direction: column;
    margin: auto;
    width: fit-content;
    margin-top: 15vh;
}
form input{
    width: 30vw;
    margin: 15px 0;
    border: 1px solid white;
    border-left: none;
    border-right: none;
    border-top: none;
    
    /* border-color: #ff914d; */
    /* border-color: white; */
    outline: none;
    text-indent: 4px;
    background: none;
    color: white;
    font-size: 1.2rem;
    margin-left: 3rem;
    font-family: 'Open Sans', sans-serif;
    
}


.nameip{
    width: 14vw;
}
.emailip{
    width: 19vw;
}
.messageip{
    text-indent: 4px;
    border: 1px solid #ff914d;
    width: 29vw;
    background: none;
    color: white;
    outline: none;
    border-left: none;
    border-top: none;
    border-right: none;
    font-family: 'Open Sans', sans-serif;
    font-size: 1rem;
    /* border-color: #ff914d; */
    border-color: white;
    margin: 15px 0;
    /* resize: none; */
    margin-left: 3rem;
}

::placeholder{
    font-size: 16px;
    font-family: 'Open Sans', sans-serif;
    color: white;
}
.submit{
    width: 7vw;
    /* background-color: #ff914d; */
    background-color: white;

    padding: 5px 0;
    /* color: white; */
    color: black;
    border: none;
    font-family: 'Open Sans', sans-serif;
    text-align: center;
    border-radius: 10px;
    font-size: 1rem;
    font-weight: 800;
    letter-spacing: 1px;
}
.fa-linkedin {
    background: #007bb5;
    color: white;
    padding: 4px 6px;
    font-size: 20px;
    text-decoration: none;
    border-radius: 6px;
    width: fit-content;
    
}

.fa-google {
    background: #dd4b39;
    color: white;
    padding: 4px 6px;
    font-size: 20px;
    text-decoration: none;
    border-radius: 6px;
    width: fit-content;
}
.fa-github{
    background: white;
    color: black;
    padding: 4px 6px;
    font-size: 20px;
    text-decoration: none;
    border-radius: 6px;
    width: fit-content;
}
.greet{
    margin-top: 10vh;
    border:  2px solid red;
    
}
.greet h1{
    text-align: center;
    font-family: 'Open Sans', sans-serif;
    font-weight: 100;
    font-size: 1.4rem;
    margin: 1rem 2rem;
    letter-spacing: 2px;
}
.notfound{
    /* border: 2px solid red; */
    background-color: #171717;
    height: 100vh;
    width: 100vw;
    color: white;
    
}
.notfound-cont{
    /* border: 2px solid black; */
    width: fit-content;
    margin: auto;
    padding-top: 45vh;
    display: flex;
    /* flex-direction: column; */
    gap: 1rem;
    align-items: center;
    text-align: center;
}
.notfound h1{
    font-size: 2rem;
    font-family: 'Open Sans', sans-serif;
    font-weight: 100;
    
}
